<template>
  <div class="header__menu-item" :class="{ 'header__menu-item--selected': isCalOpen }">
    <flat-pickr
      v-model="date"
      class="header__menu-item--datepicker"
      :config="datePickerConfig"
      @on-change="onDateSelect"
      @on-open="onDatepickerOpen"
      @on-close="onDatepickerClose"
    >
    </flat-pickr>
  </div>
</template>

<script>

import { mapActions, mapState } from 'vuex';
import flatPickr from 'vue-flatpickr-component';
import { Russian } from 'flatpickr/dist/l10n/ru';
import { Spanish } from 'flatpickr/dist/l10n/es';
import { Mandarin } from 'flatpickr/dist/l10n/zh';
import { Portuguese } from 'flatpickr/dist/l10n/pt';
import { Japanese } from 'flatpickr/dist/l10n/ja';
import { German } from 'flatpickr/dist/l10n/de';
import { DateTime} from 'luxon';

const calendarLangMap = {
  ru: Russian,
  en: null,
  es: Spanish,
  zh: Mandarin,
  pt: Portuguese,
  ja: Japanese,
  de: German
};

export default {
  name: 'Datepicker',
  components: {
    flatPickr
  },
  data() {
    return {
      date: null,
      datePickerConfig: {
        wrap: false,
        mode: 'range',
        altInput: true,
        altFormat: 'M j',
        static: true,
        locale: null
      },
      isCalOpen: false
    }
  },
  computed: {
    ...mapState([
      'currentLang',
      'dateFrom',
      'dateTo'
    ])
  },
  watch: {
    currentLang: function(lang) {
      this.datePickerConfig.locale = calendarLangMap[lang];
      // trigger date selector title to change
      this.resetDates();
    }
  },
  methods: {
    ...mapActions([
      'setFromDate',
      'setToDate',
      'fetchNews'
    ]),
    onDateSelect(newDates) {

      const [fromDate, toDate] = newDates.map(d => DateTime.fromJSDate(d).toISODate());
      
      if (!fromDate || !toDate) {
        return;
      }

      if (fromDate === this.dateFrom && toDate === this.dateTo) {
        console.log('Same dates selected');
        return;
      }

      if (fromDate) {
        this.setFromDate({date: fromDate});
      }

      if (toDate) {
        this.setToDate({date: toDate});
      }

      if (fromDate && toDate) {
        this.fetchNews();
      }

    },
    onDatepickerOpen() {
      this.isCalOpen = true;
    },
    onDatepickerClose() {
      this.isCalOpen = false;
      this.resetDates();
    },
    resetDates() {
      console.log('RESET DATES');
      this.date = [this.dateFrom, this.dateTo];
    },
  },
  created() {
    this.resetDates();
  }
}
</script>

